/*New CSS 767max mobile*/

@media screen and (max-width: 767px) {


  .main-form-container {
    text-align: center;
   width: fit-content;
    border: 1px solid black;
    display: flex;
    justify-content: center;
    flex-direction: column;
    margin-left: auto;
    margin-right: auto;
    font-size: x-small;
  }
  .main-sub-container {
    flex-wrap: wrap;
    font-weight: bold;
    width: fit-content;
  }

  .top-section-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-bottom: 0px;
    padding-bottom: 0px;
  }

  .second-section-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-content: center;
    padding-left: 15px;
    padding-right: 15px;
    margin-top: 0px;
    width: auto;
  }

  .section-header {
    width: 48.3%;
    margin-left: 15px;
    text-align: left;
    border: 1px solid black;
    margin-bottom: 0px;
  }
  .table-border3,
  .table-border3 th,
  .table-border3 td {
    border: 1px solid black;
    padding: 5px;
    border-collapse: collapse;
    text-align: left;
    padding-right: 0px;
    margin-right: 0px;
    width: 350px;
    font-size: small;
    margin-top: 05px;
  }
  .table-border4,
  .table-border4 th,
  .table-border4 td {
    border: 1px solid black;
    padding: 5px;
    border-collapse: collapse;
    text-align: left;
    padding-left: 5px;
    margin-left: 0px;
    width: 350px;
    font-size: small;
    margin-top: 05px;
  }

  .Observation-container {
    margin-top: 0px;
    margin-bottom: 0px;

    padding-bottom: 0px;
    width: auto;
  }


  .Observation-table-mb,
.Observation-table-mb th,
.Observation-table-mb td {
    border: 1px solid black;
    border-collapse: collapse;
    height: auto;
    text-align: center;
    margin-bottom: 20px;
    margin-top: 0;
}

.input-element {
    width: 100%;
    margin-left: 0px;
    margin-right: 0px;
    margin-top: 0px;
    margin-bottom: 0px;
}

.input-width {
    width: 80%;
}

.input-titles {
    width: auto;
}



  .table-border5 {
    display: block;
    width: 100%;
  }

  .table-border5 th,
  .table-border5 td {
    display: block;
    padding: 8px;
    margin-bottom: 8px;
  }

  .table-border5 th {
    font-weight: bold;
    display:  table-row;
  }

  .table-border5 thead {
    display: table-row;
  }

  .mobile-hides:nth-child(2) {
    display: none;
  }

  .table-border5 tbody .mobile-hides,
    .table-border5 thead .mobile-hides {
      display: none;
    }
  
  /* Hide the table row with class "mobile-hide" */
  .mobile-hide {
    display: none;
  }

  .Observation-table th:nth-child(n+4) {
    display: none;
  }

  /* Hide the right side columns of the table */
  .Observation-table td:nth-last-child(-n+8) {
    display: none;
  }

  .footer-sub {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;

    margin-left: 0px;
  }

  .text {
    font-size: 18px;
    text-align: start;
    padding-left: 15px;
  }

}


